import React from 'react';
import {Layout, Menu} from 'antd';
import {FileOutlined} from "@ant-design/icons";
import {marked} from "marked";
import hljs from 'highlight.js';
import {ItemType, MenuItemType} from "antd/es/menu/hooks/useItems";
import 'highlight.js/styles/github.css'

function App() {

    const [treeData, setTreeData] = React.useState<MenuItemType[]>([]);
    const [textMap, setTextMap] = React.useState(new Map());
    const [selected, setSelected] = React.useState<string>();
    const [update, setUpdate] = React.useState(0);
    const [html, setHtml] = React.useState('');

    function read(value: object | string, key: string, parent: string, timestamp: number, count: number): any {
        if (typeof value === 'string') {
            let dataNode: any = {
                label: key,
                key: parent,
                icon: <FileOutlined/>,
            }
            fetch(parent + '?timestamp=' + timestamp).then(res => {
                res.blob().then(blob => {
                    dataNode.label += '(' + (blob.size / 1024).toFixed(2) + 'KB)';
                    blob.arrayBuffer().then(res => {
                        let text = new TextDecoder('utf-8').decode(res);
                        marked.setOptions({
                            highlight: function (code) {
                                return hljs.highlightAuto(code).value;
                            }
                        })
                        setUpdate(count);
                        let html;
                        if (key.endsWith('png') || key.endsWith('jpg')) {
                            html = `<img alt=[${key.substring(0, key.lastIndexOf('.'))}] src="${parent}"/>`
                        } else if (!key.includes('.min')) {
                            html = marked.parse('```' + key.substring(key.lastIndexOf('.') + 1) + '\n' + text + '\n```');
                        } else {
                            html = `<div>${text}</div>`;
                        }
                        textMap.set(parent, html);
                        setTextMap(textMap);
                    });
                });
            })
            return dataNode;
        } else {
            let children: any[] = []
            let dataNode: ItemType = {
                label: key,
                key: parent,
                children,
            }
            Object.entries(value).forEach(([key, value]) => {
                children.push(read(value, key, parent + '/' + key, timestamp, count++));
            });
            return dataNode;
        }
    }

    React.useEffect(() => {
        fetch('https://zzxt0019.github.io/bilishield/data/data.json?timestamp=' + new Date().getTime()).then(res => {
            res.json().then(json => {
                let main = read(json.data, '', 'https://zzxt0019.github.io/bilishield/data', json.timestamp, 0);
                setTreeData(main.children);
            });
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    React.useEffect(() => {
        setTimeout(() => setTreeData([...treeData]))
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [update])

    return <>
        <Layout>
            <Layout.Sider style={{width: 256}}>
                <Menu style={{height: '100%'}}
                      mode={'inline'}
                      onSelect={(event) => {
                          console.log(event.key, textMap)
                          if (selected === event.key) {
                              setHtml('');
                          } else {
                              setHtml(textMap.get(event.key));
                              setSelected(event.key);
                          }
                          console.log(html)
                      }}
                    // showIcon={true}
                    // onSelect={(keys) => setCheckedKey(checkedKey === keys[0] ? undefined : keys[0])}
                      items={treeData as any}
                >
                </Menu>
            </Layout.Sider>
            <Layout.Content>
                {selected && <a href={selected}>{selected}</a>}
                <div className="show-html" dangerouslySetInnerHTML={{__html: html}}></div>
            </Layout.Content>
        </Layout>
    </>;
}

export default App;
